import React, { Fragment, useState, useEffect } from 'react';
import ShoppingCard from './components/ShoppingCard';
import style from './index.module.less';
import api from './api';
import { useLocation } from 'react-router-dom';
import qs from 'qs';
// import 'https://es.echatsoft.com/open/staff/jssdk-1.0.0.js';
const arr = [
  {
    img: '',
    title: '',
    img2: '',
    name: ' 103',
    color: '#E2CDB5',
    colorName: '',
    money: '1457.00',
  },
  {
    img: '',
    title: '',
    img2: '',
    name: 'Teint Couture Cushio  103',
    color: '#F4CB9D',
    colorName: '',
    money: '507.00',
  },
  {
    img: '',
    title: '',
    img2: '',
    name: 'Bloom Couture Eyes',
    color: '',
    colorName: '',
    money: '430.00',
  },
  {
    img: '',
    title: '',
    img2: '',
    name: ' 103',
    color: '#E2CDB5',
    colorName: '',
    money: '1457.00',
  },
];
export default function ShoppingCar() {
  const [list, setList] = useState(arr);
  const [permit, setPermit] = useState({});
  const params = useLocation();
  const sendMsg = (msg: any) => {
    var visEvt = msg;
    // console.log(permit);
    // visEvt.timestamp = permit.timestamp;
    // visEvt.nonce = permit.nonce;
    // visEvt.signature = permit.signature;
    // // debugger;
    window.echatSDK.pushVisitorEvent(visEvt);
  };
  useEffect(() => {
    const urlParams = params.search.split('?')[1];
    console.log(qs.parse(urlParams));
    const param = qs.parse(urlParams);
    window.echatSDK.config({
      debug: true, // ,sdk
      timestamp: param.timestamp, // 
      nonce: param.nonce, // 
      signature: param.signature, // 
    });
    window.echatSDK.ready(function () {
      console.log('');

      // configreadyconfig
    });
    window.echatSDK.error(function (res: any) {
      console.log(2, res);
      // configerrorres
    });
    api.getData({ companyId: '531521' }).then((res: any) => {
      // console.log(res);
      if (res.code === 200) {
        setList(res.data || []);
        // setUserInfo(res.data);
      }
    });
    return () => {};
  }, []);

  return (
    <div className={style.shopping_box}>
      {list.map((item, index) => (
        <Fragment key={index}>
          <ShoppingCard option={item} onSend={sendMsg} />
        </Fragment>
      ))}
    </div>
  );
}
