// import React from 'react';
import style from '../index.module.less';
import classnames from 'classnames';
import Card from './Card';
import { useState, useEffect } from 'react';
let newList: any = [];
// const lineList = [
//   [
//     {
//       id: 1,
//       name: '',
//       tip: '',
//     },
//     {
//       id: 2,
//       name: '',
//       tip: '',
//     },
//   ],
//   [
//     {
//       id: 3,
//       name: '',
//       tip: '',
//     },
//     {
//       id: 4,
//       name: '',
//       tip: '',
//     },
//   ],
//   [
//     {
//       id: 5,
//       name: 'a',
//       tip: '',
//     },
//     {
//       id: 6,
//       name: 'a',
//       tip: '',
//     },
//   ],
// ];
export default function CardList(props: any) {
  const [list, setList] = useState([
    {
      id: 1,
      name: '',
      tip: '',
      imgInfo: {
        tip1: '0.52',
        tip2: '',
        src: '',
      },
    },
    {
      id: 2,
      name: '',
      tip: '',
      imgInfo: {
        tip1: '',
        tip2: '',
        src: '',
      },
    },
    {
      id: 3,
      name: '',
      tip: '',
      imgInfo: {
        tip1: '',
        tip2: '',
        src: '',
      },
    },
    {
      id: 4,
      name: '',
      tip: '',
      imgInfo: {
        tip1: '',
        tip2: '',
        src: '',
      },
    },
    {
      id: 5,
      name: 'a',
      tip: '',
      imgInfo: {
        tip1: '',
        tip2: '',
        src: '',
      },
    },
    {
      id: 6,
      name: 'a',
      tip: '',
      imgInfo: {
        tip1: '',
        tip2: '',
        src: '',
      },
    },
  ]);
  const [lineList, setLinelist] = useState([]);
  // const [width, setWidth] = useState(0);
  const resizeUpdate = () => {
    // 
    let w = window.innerWidth;
    w = w - 40;
    let colNum = Math.floor(w / 150) || 1;
    // console.log('1', colNum);

    if (colNum !== 1) {
      let newW = w - 30 * (colNum - 1);
      colNum = Math.floor(newW / 150) || 1;
    }

    newList = [];
    // 
    for (let i = 1; i <= colNum; i++) {
      newList.push([]);
    }
    list.map((item, index) => {
      newList[index % colNum].push(item);
    });

    // setWidth(w);
    for (let cI = colNum - 1; cI >= 0; cI--) {
      if (!newList[cI].length) {
        newList.splice(cI, 1);
      }
    }
    setLinelist(newList);
  };

  useEffect(() => {
    // 
    resizeUpdate();
    window.addEventListener('resize', resizeUpdate);
    return () => {
      // 
      window.removeEventListener('resize', resizeUpdate);
    };
  }, []);
  useEffect(() => {
    //  
    // resizeUpdate();
  }, [list]);
  return (
    <div className={style.content_box}>
      {lineList.length &&
        lineList.map((lItem: any, Lindex) => (
          <div key={Lindex} className={style.card_box}>
            {lItem.length &&
              lItem.map((item: any) => {
                return (
                  <div
                    className={classnames(style.card, {
                      [`${style.card_one}`]: Lindex % 2 === 0,
                      [`${style.card_two}`]: Lindex % 2 !== 0,
                    })}
                    key={item.id}
                  >
                    <Card option={item} />
                  </div>
                );
              })}
          </div>
        ))}
    </div>
  );
}
