import React, { Fragment, useState, useEffect } from 'react';
import CollectCard from './components/CollectCard';
import style from './index.module.less';
import api from './api';
import { useLocation } from 'react-router-dom';
import qs from 'qs';
const test = [
  {
    img: '',
    isLike: 0,
    title: 'FENTY BEAUTY  7.1g',
    like: '4.2',
    money: '246.00',
  },
  {
    img: '',
    isLike: 1,
    title: 'CAUDALIE 30ml',
    like: '5.0',
    money: '528.00',
  },
  {
    img: '',
    isLike: 1,
    title: '- ',
    like: '4.2',
    money: '23,551.00',
  },
  {
    img: '',
    isLike: 0,
    title: '- ',
    like: '3.8',
    money: '6,200.00',
  },
  {
    img: '',
    isLike: 1,
    title: 'FENTY BEAUTY  7.1g',
    like: '4.2',
    money: '246.00',
  },
  // {
  //   img: '',
  //   isLike: 1,
  //   title: 'FENTY BEAUTY  7.1g',
  //   like: '4.2',
  //   money: '246.00',
  // },
];
export default function Collection() {
  const [list, setList] = useState(test);
  const [rowCount, setRowCount] = useState(0);
  const params = useLocation();
  const sendMsg = (msg: any) => {
    var visEvt = msg;
    // console.log(permit);
    // visEvt.timestamp = permit.timestamp;
    // visEvt.nonce = permit.nonce;
    // visEvt.signature = permit.signature;
    // // debugger;
    window.echatSDK.pushVisitorEvent(visEvt);
  };
  const resizeUpdate = () => {
    // 
    let w = window.innerWidth;
    w = w - 40; // 
    let colNum = Math.floor(w / 150) || 1;

    if (colNum !== 1) {
      let newW = w - 30 * (colNum - 1);
      colNum = Math.floor(newW / 150) || 1;
    }

    setRowCount(colNum);
    // setWidth(w);
  };
  useEffect(() => {
    // 
    const urlParams = params.search.split('?')[1];
    console.log(qs.parse(urlParams));
    const param = qs.parse(urlParams);
    window.echatSDK.config({
      debug: true, // ,sdk
      timestamp: param.timestamp, // 
      nonce: param.nonce, // 
      signature: param.signature, // 
    });
    window.echatSDK.ready(function () {
      console.log('');

      // configreadyconfig
    });
    window.echatSDK.error(function (res: any) {
      console.log(2, res);
      // configerrorres
    });
    api.getData({ companyId: '531521' }).then((res: any) => {
      console.log(res);
      if (res.code === 200) {
        setList(res.data || []);
        // setUserInfo(res.data);
      }
    });
    resizeUpdate();
    window.addEventListener('resize', resizeUpdate);
    return () => {
      // 
      window.removeEventListener('resize', resizeUpdate);
    };
  }, []);
  useEffect(() => {
    //  
    resizeUpdate();
  }, [rowCount]);
  return (
    <div className={style.collection_box}>
      {list.map((item, index) => (
        <Fragment key={index}>
          <CollectCard
            rowCount={rowCount}
            index={index}
            option={item}
            onSend={sendMsg}
          />
        </Fragment>
      ))}
    </div>
  );
}
